import React from 'react';

class Header extends React.Component {
  render() {
    return (
      <div className="header">
        <img className="header-logo" src="./logo.png" height="80" alt=""/>
        <div className="header-menu">
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel/blob/master/README.md" className="header-menuItem" target="_blank" rel="noreferrer">
            ABOUT
          </a>
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel/releases" className="header-menuItem" target="_blank" rel="noreferrer">
            DOWNLOAD
          </a>
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel" className="header-menuItem" target="_blank" rel="noreferrer">
            PROJECT
          </a>
        </div>
        <div className="header-recalbox-container">
          <a className="header-recalbox" href="https://www.recalbox.com/" target="_blank" rel="noreferrer">
            RECALBOX 8.0+ READY
            <img className="header-recalbox-logo" src="./recalbox_logo.png" width="40" alt=""/>
          </a>
        </div>
      </div>
    );
  }
}

export default Header;
